.header {
    background: var(--background);
    border-bottom: 1px solid var(--border);

    /* position: fixed; */
    top: 0;
    width: 100%;
}

.header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 60px;

    max-width: 957px;
    margin: 0 auto;
    width: 90%;
}

.header .container .logo {
    width: 110px;
    cursor: pointer;
}

.input-fake {
    background: var(--background-main);
    border-radius: 3px;

    padding: 3px 10px;

    border: 1px solid var(--border);

    display: flex;
    align-items: center;
}

.input-fake input {
    font-size: 14px;
    padding: 0 7px;
    border: 0;
}

.menu-icons {
    display: flex;
    align-items: center;
    gap: 16px;
    cursor: pointer;
}

.menu-icons .img-user{
    width: 26px;
    height: 26px;
    border-radius: 100%;
}