    color: #64748b;
    border-radius: 2px;
    margin-top: 5px;
    padding: 8px 16px 8px 16px;
    outline: none;
  }
  .register-button {
    font-weight: bold;
    font-size: 14px;
    color: #ffffff;
    height: 40px;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 2px;
    background-color: #0b69ff;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    outline: none;
  }
  
  .error-message {
    align-self: start;
    font-size: 12px;
    margin-top: 3px;
    margin-bottom: 0px;
    font-family: 'Roboto';
    line-height: 16px;
    color: #ff0b37;
  }
  
  .website-heading {
    font-family: 'Roboto';
    font-size: 30px;
    font-size: 500;
    color: #475569;
  }
  
  @media screen and (max-width: 768px) {
    .website-heading {
      font-size: 18px;
    }
  }
  
  .signup-website-logo-desktop-img {
    height: 50px;
    width: 90px;
  }
  